body {
  margin: 0;
  background-color: #121212;
  color: white;
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
  background-color: #383838;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar ul {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.navbar a {
  color: white;
  text-decoration: none;
}
.movie-card {
  background-color: #272727;
  padding: 1rem;
  border-radius: 8px;
  margin: 1rem;
  transition: transform 0.5s ease;

  /* pastikan semua card punya ukuran konsisten */
  width: 150px;           /* lebar tetap */
  display: flex;
  flex-direction: column;
  align-items: center;    /* biar konten di tengah */
}

/* ukuran gambar menyesuaikan lebar container */
.movie-card img {
  width: 100%;         
  height: 225px;        /* atau pakai auto kalau mau proporsional */
  object-fit: cover;
  border-radius: 4px;
}

/* atur judul agar tidak melebar */
.movie-card h3 {
  font-size: 1rem;
  margin: 0.5rem 0 0.25rem;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
}

.movie-card:hover {
  transform: scale(1.02);
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

}

button, input {
  background-color: #333;
  border: none;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  margin: 0.5rem 0.5rem;
}
